import React,{useState,useEffect} from 'react'

function Home({addToFav,addFavorite,getItem}) {

    const [current, setcurrent] = useState({})
  const [val, setval] = useState('')
  const [temp,setTemp] = useState([])
  const [forcast, setForcast] = useState([])
  const [err,setErr] = useState(false);
  const [indicate, setindicate] = useState(false)
  

  const keyId = "wLw9KaXCQZlkNT7Xm12RtcHoP2A2WCkT"

  const locationsId = `http://dataservice.accuweather.com/locations/v1/cities/autocomplete?apikey=${keyId}&q=`


  var days = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
  var dayName = days[new Date().getDay()];

  
    let ansTemp = temp.map(item =>{
      return  item.Temperature.Imperial.Value
    })

    let ansText = temp.map(item=>{
        return item.WeatherText
    })

    let ansFive = forcast.map(item =>{
        return item.Temperature.Minimum.Value
    })

    
   let btnAns =  addFavorite.some(item =>{
return item.name === current.name
    })



  const checkIfLetters = (v) =>{
    let reg = /^[a-zA-Z\s]*$/;
    let res =reg.test(v)
    return res
  }

  

  useEffect(() => {

      if(getItem){
    
        const{id,name} = getItem

        setErr(true)
        fetch(`http://dataservice.accuweather.com/currentconditions/v1/${id}?apikey=${keyId}`)
          .then(res1 => res1.json())
           .then(result1 => setTemp(result1))
           .then( setcurrent({id: id,name: name}))
           
           
        
        
      
          fetch(`http://dataservice.accuweather.com/forecasts/v1/daily/5day/${id}?apikey=${keyId}`)
          .then(res2 => res2.json())
           .then(result2 => setForcast(result2.DailyForecasts))
           
          

      }else if(!getItem){
        setErr(true)
       

        // 
            fetch(`http://dataservice.accuweather.com/currentconditions/v1/215854?apikey=${keyId}`)
          .then(res1 => res1.json())
           .then(result1 => setTemp(result1))
           .then( setcurrent({id:'215854',name:'Tel Aviv'}))
           .catch((error) =>{
            console.log(error);
           alert('Error loading data')
           setval('')
          })
           
        
        
      
          fetch(`http://dataservice.accuweather.com/forecasts/v1/daily/5day/215854?apikey=${keyId}`)
          .then(res2 => res2.json())
           .then(result2 => setForcast(result2.DailyForecasts))
           .then( setval(''))
           .catch((error) =>{
            console.log(error);
           alert('Error loading data')
           setval('')
          })
      
      
      }

       
     
  }, [getItem])


  useEffect(() => {

  
    if (val) {

        let isLetters = checkIfLetters(val)

        if (isLetters) {

            let newLoc = `${locationsId}${val}`
      setErr(false)
      fetch(newLoc)
         
          .then(res => res.json())
         
             .then(data => setcurrent({id:data[0].Key,name:data[0].LocalizedName}))
            
            
             .catch((error) =>{
               console.log(error);
              alert('Error... no such search Found')
              setval('')
             })
    }else{
        alert('Must have English letters only!!!')
        setcurrent({})
        setErr(false)
        setval('')
    }

    

    }}, [val])
        

      
    
     
     
    
    
    const handleChange = (e) =>{
    let val = e.target.value
    setval(val)
    
    }
    
    
    
    const search = evt => {
    
    if (evt.key === 'Enter') {

        if(val){
            let yo = current.id
            
            
            setErr(true)
    
            // let newTemp = `http://dataservice.accuweather.com/currentconditions/v1/${yo}?apikey=${keyId}`
            // console.log(newTemp);
                fetch(`http://dataservice.accuweather.com/currentconditions/v1/${yo}?apikey=${keyId}`)
              .then(res1 => res1.json())
               .then(result1 => setTemp(result1))
               .catch((error) =>{
                console.log(error);
               alert('Error loading data')
               setval('')
              })
               
            
            
          
              fetch(`http://dataservice.accuweather.com/forecasts/v1/daily/5day/${yo}?apikey=${keyId}`)
              .then(res2 => res2.json())
               .then(result2 => setForcast(result2.DailyForecasts))
               

               .catch((error) =>{
                console.log(error);
               alert('Error loading data')
               setval('')
              })
               
           }else{
               alert('Please insert English letters only')
           }
        }
      
      
      
    }

    return (
        <div className='body-temp' >
            <div className='title'>
            <h1>Wheather App</h1>
            <br/>
            <br/>
            <h3 style={{color: 'green'}}>Press Enter to Search</h3>
<input placeholder='Enter City...' onKeyPress={ search } value={val} onChange={handleChange} type="text"/>
            </div>
           <br/>
           <br/>
           <br/>
    <div className='body-search'>
    {err ? <div>
  
  <h1>{ current.name }</h1>
  <h2>{dayName}</h2>
  </div> : null}

{ err && temp.map((item,index) =>(
<div key={index}>
<h1>{item.Temperature.Imperial.Value}F•</h1>
<h1>{item.WeatherText}</h1>
  </div>

))}

<br/>
<br/>


{ err && forcast.map((item,i) =>(
  
  <div className='five-day'  key={i}>
        <h2>{days[(new Date().getDay() + i + 1) % days.length]}</h2>
    <h1>{item.Temperature.Minimum.Value}F•</h1>
    </div>
 
 
 
    

))}


<br/>
<br/>
<h3 style={{color:'red'}}>{btnAns && err && 'Saved in favorites list⭐️'}</h3>

  {err && <button onClick={()=> addToFav(current.name,ansTemp,ansText,ansFive,btnAns,current.id)}>{ btnAns ? 'Remove from favorites' :'Add to favorites'}</button>}
  </div>

      
    </div>
           

    )
}

export default Home
