import React,{useState} from 'react';
import Header from './header'
import Home from './Home'
import Favorites from './Favorites'
import NavSideDrawer from './NavSideDrawer'
import BackDrop from './BackDrop'

import {Switch,Route,BrowserRouter as Router} from 'react-router-dom';

import './App.css';
import SideDrawer from './SideDrawer';

function App() {

  const [addFavorite, setAddFav] = useState([])
  const [getItem,setGetItem] = useState(null)
  const [sideDrawer, setsideDrawer] = useState(false)
  


const addToFav = (v,t,a,f,btn,idz) =>{
if (btn) {
 let newFav =  addFavorite.filter(item =>{
  return item.name !== v
 })
 
 setAddFav([...newFav])
}else{
  let temperture = t.join('')
  
  
  
  setAddFav([...addFavorite,{name:v,temper:temperture,id:idz}])
  }
}


const findItem = (idz,index,n) =>{
  

setGetItem({index:index,id:idz,name:n})
}


 const drawerToggleClick = () =>{
setsideDrawer((prevState)=>{
  return{sideDrawer: !prevState.sideDrawer}
})
 }

 const backDropClick=()=>{
   setsideDrawer(false)
 }


let backdrop;

if(sideDrawer){
  
  backdrop = <BackDrop click={backDropClick}/>
}


  return (
    
    <div style={{height:'100%'}}>

    <Router>
    <Header drawerClickHandler={drawerToggleClick}/>
    <NavSideDrawer show={sideDrawer} click={backDropClick}/>
    {backdrop}
          <Switch>
          <Route exact path='/' component={()=>{return <Home getItem={getItem} addFavorite={addFavorite} addToFav={addToFav}/>}}/>
          <Route exact path='/favorites' component={()=>{return <Favorites findItem={findItem} addFavorite={addFavorite}/>}}/>
         
            
            
          </Switch>
        </Router>



</div>
)
  }


export default App;
