import React from 'react'
import {NavLink} from 'react-router-dom';
import './NavSideDrawer.css'

function NavSideDrawer({show,click}) {

    let drawerClasses = ['side-drawer']
    if (show) {
        drawerClasses = ['side-drawer open']
    }
    return (
        <div className={drawerClasses}>
            <div className='NavLinks'>
            <NavLink style={{color:'black',margin:'10px'}} onClick={click} to='/'>Home</NavLink>
            <NavLink style={{color:'black'}} onClick={click} to='/favorites'>Favorites</NavLink>
            </div>
       
        </div>
    )
}

export default NavSideDrawer
