import React from 'react'
import ItemFav from './ItemFav'
import {Link} from 'react-router-dom'

function Favorites({addFavorite,findItem}) {
    return (
        <div className='favs'>
            <h1 style={{marginTop:'20px'}}>Favorites</h1>
            
            { addFavorite.map((item,index) =>{
                
                return <Link to='/' key={index}><button  className='btnItem' onClick={()=>findItem(item.id,index,item.name)}><ItemFav  key={index} item={item}/></button></Link>
            })}
        </div>
    )
}

export default Favorites
